import { useEffect, useState } from 'react';

import { Steps, Form, notification } from 'antd';

import { MinusCircleOutlined, PlusOutlined } from '@ant-design/icons';

import React from 'react';

import { motion } from "framer-motion";

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import { faCircleNotch } from "@fortawesome/free-solid-svg-icons"

import "../assets/style/main.css";

import { useLocation } from "react-router-dom";

const { Step } = Steps;

const Main = () => {

    const [currentStep, setStep] = useState(0);
    const [company, setCompany] = useState("");
    let [domains, setDomains] = useState([]);
    const [ip, setIp] = useState("");
    const [loading, setLoading] = useState(false);
    const [onComplete, setOnComplete] = useState(false);
    const [disabled, setDisabled] = useState(true);
    const search = useLocation().search;
    const url = new URLSearchParams(search).get('url');

    useEffect(() => {
        if (onComplete) {
            setCompany("")
            setDomains([])
            setIp("")
            setOnComplete(false)
        }
    }, [onComplete])

    useEffect(() => {
        if (company.length === 0) {
            setDisabled(true)
        } else {
            setDisabled(false)
        }
    }, [company])

    useEffect(() => {
        window.onbeforeunload = async function () {
            return true;
        };

        return () => {
            window.onbeforeunload = null;
        };
    }, []);

    const getCharLimitError = () => {
        notification.error({ message: "Enter String below 300 character!" })
    }

    const onFinish = async () => {
        setLoading(true)
        fetch(`${url}`, {
            method: "POST",
            body: JSON.stringify({
                cname: company,
                subdomains: domains,
                ip
            }),
            headers: {
                "Content-type": "application/json; charset=UTF-8"
            }
        }).then(() => {
            notification.success({ message: "Updated successfully!" })
            setOnComplete(true);
        }).catch((e) => {
            notification.error({ message: "Something went wrong" })
        })
        setLoading(false);
    }

    const getMainContent = () => {
        switch (currentStep) {
            case 0:
                return (
                    <div className="cxt">
                        <p className="heading">
                            First, Provide Your Company name!
                        </p>
                        <div className="main-area">
                            <div className='field'>
                                <input type="text" name="domain" onChange={(e) => {
                                    if (e.target.value.length <= 300) {
                                        setCompany(e.target.value)
                                    } else {
                                        getCharLimitError()
                                    }
                                }} />
                                <label htmlFor="domain" className='domain-label'>Enter Company Name</label>
                            </div>
                            <div className="note">
                                <span className="note-sym">*</span>{" "} mean mandatory fields, remaining are optional
                            </div>
                            <div className="nxt-btn">
                                <motion.button disabled={disabled} whileHover={{ scale: 1.2 }} transition={{ ease: "linear" }} whileTap={{ scale: 0.8 }} className="btn-continue" onClick={() => setStep(1)}>
                                    {loading && <FontAwesomeIcon icon={faCircleNotch} className="fa-spin" style={{ marginRight: "8px" }} />}
                                    Continue
                                </motion.button>
                            </div>
                        </div>
                    </div>
                )
            case 1:
                return (
                    <div className="cxt">
                        <p className="heading">
                            Next, Do you have list of Domains ?
                        </p>
                        <div className="main-area">
                            <Form name="dynamic_form_nest_item">
                                <Form.List
                                    name="subdomains"
                                >
                                    {(fields, { add, remove }, { errors }) => (
                                        <>
                                            {fields.map(({ key, name: current, ...restField }) => (
                                                <Form.Item
                                                    required={false}
                                                    key={key}
                                                    className="dynamic-field"
                                                >
                                                    <Form.Item
                                                        {...restField}
                                                    >
                                                        <div className="field">
                                                            <input type="text" name="subdomain" className='sub-domain-field' onChange={(e) => {
                                                                if (e.target.value.length <= 300) {
                                                                    domains[current] = e.target.value;
                                                                    setDomains(domains);
                                                                } else {
                                                                    getCharLimitError()
                                                                }
                                                            }} />
                                                            <label htmlFor="subdomain">Enter Sub domain</label>
                                                        </div>
                                                        {fields.length > 1 ? (
                                                            <MinusCircleOutlined
                                                                className="dynamic-delete-button"
                                                                onClick={() => {
                                                                    domains.splice(current, 1)
                                                                    setDomains(domains)
                                                                    remove(current)
                                                                }}
                                                            />
                                                        ) : null}
                                                    </Form.Item>
                                                </Form.Item>
                                            ))}
                                            <Form.Item>
                                                <div className="new-field-wrapper">
                                                    <PlusOutlined className="prefix-icon" />
                                                    <button
                                                        className='new-field'
                                                        onClick={() => {
                                                            if (domains.length <= 100) {
                                                                add();
                                                            } else {
                                                                notification.error({ message: "subdomain limit exceed", description: "you're allowed to add only 100 sub domains" })
                                                            }
                                                        }}
                                                    >
                                                        Add field
                                                    </button>
                                                </div>
                                                <Form.ErrorList errors={errors} />
                                            </Form.Item>
                                        </>
                                    )}
                                </Form.List>
                                <Form.Item>
                                    <div className="nxt-btn">
                                        <motion.button disabled={loading} whileHover={{ scale: 1.2 }} transition={{ ease: "linear" }} whileTap={{ scale: 0.8 }} className="btn-continue" onClick={() => setStep(2)}>
                                            {loading && <FontAwesomeIcon icon={faCircleNotch} className="fa-spin" style={{ marginRight: "8px" }} />}
                                            Continue
                                        </motion.button>
                                    </div>
                                </Form.Item>
                            </Form>
                        </div>
                    </div>
                )
            case 2:
                return (
                    <div className="cxt">
                        <p className="heading">
                            Last, We need your static IP!
                        </p>
                        <div className="main-area">
                            <div className="field">
                                <input type="text" className='domain-field' onChange={(e) => {
                                    if (e.target.value.length <= 300) {
                                        setIp(e.target.value)
                                    } else {
                                        getCharLimitError()
                                    }
                                }} />
                                <label htmlFor="ip">Enter Ip</label>
                            </div>
                            <div className="nxt-btn">
                                <motion.button disabled={loading} whileHover={{ scale: 1.2 }} transition={{ ease: "linear" }} whileTap={{ scale: 0.8 }} className="btn-continue" onClick={onFinish}>
                                    {loading && <FontAwesomeIcon icon={faCircleNotch} className="fa-spin" style={{ marginRight: "8px" }} />}
                                    start
                                </motion.button>
                            </div>
                        </div>
                    </div>
                )
            default:
                return <></>
        }
    }

    if (!url) {
        return <></>
    }

    return (
        <>
            <div className="topbar">
                <p className="title">Sec Team 👾</p>
                <p className="greet">Welcome, Cappriciosec!</p>
            </div>
            <div className="main-container">
                <div className="step-side">
                    <Steps current={currentStep} progressDot={<></>} direction='vertical'>
                        <Step className={currentStep >= 0 && "showTail"} title={<div className='domain-step-label'>Add Company</div>} onStepClick={() => setStep(0)} />
                        <Step disabled={disabled} className={currentStep >= 1 && "showTail"} title="Add Subdomains" onStepClick={() => setStep(1)} />
                        <Step disabled={disabled} className={currentStep >= 2 && "showTail"} title="Add IP" onStepClick={() => setStep(2)} />
                    </Steps>
                </div>
                <div className="cxt-side">
                    {getMainContent()}
                </div>
            </div>
        </>
    )
}

export default Main;